import { cwd } from 'node:process';
import type { CommandServices } from '@wix/cli-command-contract';
import { loadProjectModel } from '../model.js';
import { authenticate } from '../authentication.js';
import { syncTypesWithAuth } from './sync-types-with-auth.js';

export async function syncTypes({ biLogger, errorReporter }: CommandServices) {
  const model = await loadProjectModel(cwd());
  const authState = await authenticate({
    metaSiteId: model.metaSiteId,
    biLogger,
    errorReporter,
  });

  return syncTypesWithAuth(model, { authState });
}
