// src/public-files-types.ts
async function writePublicFilesTypes(projectFolder) {
  const jsconfigPath = getPublicConfigPath(projectFolder);
  await mkdir(dirname(jsconfigPath), { recursive: true });
  await writeJson(jsconfigPath, {
    extends: relative(
      dirname(jsconfigPath),
      publicFilesTsConfigPath(projectFolder)
    ),
    compilerOptions: {
      composite: true,
      noEmit: false,
      paths: {
        "backend/*.web.js": [
          join3(
            relative(dirname(jsconfigPath), getBackendFolder(projectFolder)),
            "*.web.js"
          )
        ],
        "backend/*.web": [
          join3(
            relative(dirname(jsconfigPath), getBackendFolder(projectFolder)),
            "*.web.js"
          )
        ]
      },
      outDir: "build",
      rootDir: relative(dirname(jsconfigPath), projectFolder)
    },
    include: [
      join3(
        relative(dirname(jsconfigPath), getPublicFolder(projectFolder)),
        "**/*"
      ),
      join3(
        relative(dirname(jsconfigPath), getBackendFolder(projectFolder)),
        "**/*.web.js"
      ),
      relative(dirname(jsconfigPath), getBackendDTSPath(projectFolder))
    ]
  });
}

